import Batcher from '@/collections/Batcher';
import di from '@/DependencyInjection';
import { ProjectServiceServiceId } from '@/di/services/ProjectServiceInjector';
import { type IProjectStore } from '@interfaces/stores/IProjectStore';
import { ProjectSchema, GetProjectsSchema, type ProjectData, type CreateProjectData, type GetProjectsData, type UpdateProjectData } from '@strife/common';
import { useQuery } from '@tanstack/react-query';
import { injectable } from 'inversify';

@injectable()
export class ProjectStore implements IProjectStore {
  
  private _projectService = di.get(ProjectServiceServiceId);
  private _batcher = new Batcher<string, ProjectData>(this.onFetch.bind(this));

  public createProject(createProjectData: CreateProjectData): Promise<ProjectData> {
    return this._projectService.createProject(createProjectData);
  }

  public deleteProject(projectId: string): Promise<void> {
    return this._projectService.deleteProject(projectId);
  }

  public getProject(projectId: string): ReturnType<typeof useQuery<ProjectData>> {
    return useQuery({ 
      queryKey: this.getQueryKey(projectId), 
      queryFn: (): Promise<ProjectData> => this._batcher.request(projectId) 
    });
  }

  public getProjects(getProjectsData: GetProjectsData): ReturnType<typeof useQuery<ProjectData[]>> {
    return useQuery({
      queryKey: [ typeof ProjectSchema, getProjectsData ],
      queryFn: (): Promise<ProjectData[]> => this._projectService.getProjects(getProjectsData)
    });
  }

  public async updateProject(projectId: string, updateProjectData: UpdateProjectData): Promise<ProjectData> {
    const projectData = await this._projectService.updateProject(projectId, updateProjectData);
    // TODO: Figure out how to update the cached query data on the QueryClient
    return projectData;
  }

  private getQueryKey(projectId: string): unknown[] {
    return [ typeof ProjectSchema, projectId ];
  }

  private async onFetch(ids: string[]): Promise<Map<string, ProjectData>> {
    const getProjectsData = await GetProjectsSchema.parseAsync({ ids });
    const projectDatas = await this._projectService.getProjects(getProjectsData);
    const projectEntries = projectDatas.map((projectData: ProjectData): [string, ProjectData] => [ projectData.id, projectData ]);
    return new Map(projectEntries);
  }

}

export default ProjectStore;